import * as React from "react"
import { useStaticQuery, graphql } from "gatsby"
import { GatsbyImage } from "gatsby-plugin-image"
import { Bounce } from "react-reveal"
import "./style.scss"

const Superiority = ({}) => {
  const query = useStaticQuery(graphql`
    query {
      icon51: file(relativePath: { eq: "i-.png" }) {
        childImageSharp {
          gatsbyImageData(layout: CONSTRAINED)
        }
      }
      icon52: file(relativePath: { eq: "i-.png" }) {
        childImageSharp {
          gatsbyImageData(layout: CONSTRAINED)
        }
      }
      icon53: file(relativePath: { eq: "i-.png" }) {
        childImageSharp {
          gatsbyImageData(layout: CONSTRAINED)
        }
      }
      icon54: file(relativePath: { eq: "i-.png" }) {
        childImageSharp {
          gatsbyImageData(layout: CONSTRAINED)
        }
      }
      icon55: file(relativePath: { eq: "i-.png" }) {
        childImageSharp {
          gatsbyImageData(layout: CONSTRAINED)
        }
      }
      icon56: file(relativePath: { eq: "i-.png" }) {
        childImageSharp {
          gatsbyImageData(layout: CONSTRAINED)
        }
      }
    }
  `)

  const data = [
    {
      title: "",
      describe:
        "SmartCLOUD Compute",
      img: query.icon51.childImageSharp.gatsbyImageData,
    },
    {
      title: "",
      describe:
        "",
      img: query.icon52.childImageSharp.gatsbyImageData,
    },
    {
      title: "",
      describe: "",
      img: query.icon53.childImageSharp.gatsbyImageData,
    },
    {
      title: "",
      describe:
        "TL9000ISO9001ISO14001ISO20000ISO27001ISO27017",
      img: query.icon54.childImageSharp.gatsbyImageData,
    },
    {
      title: "",
      describe:
        "//",
      img: query.icon55.childImageSharp.gatsbyImageData,
    },
    {
      title: "",
      describe: "7x24",
      img: query.icon56.childImageSharp.gatsbyImageData,
    },
  ]
  return (
    <div
      css={{
        backgroundColor: "#fff",
      }}
    >
      <div className="container">
        <div className="superiority">
          <div className="title" css={{ textAlign: "center" }}>
            <h3></h3>
          </div>
          <div className="box row" css={{ overflow: "hidden" }}>
            {data.map((item, d) => (
              <div key={item.title} className="col-6 col-md-4 rcol">
                <Bounce top fraction={0.2} delay={d * 100} ssrFadeout>
                  <div className="box-item">
                    <GatsbyImage
                      image={item.img}
                      alt={item.describe}
                      title={item.title}
                    />
                    <h3>{item.title}</h3>
                    <p>{item.describe}</p>
                  </div>
                </Bounce>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}

export default Superiority
